import React, {useState, useEffect} from "react";
import './styles.css'
import { createUseStyles } from "react-jss";
import bg from '../imgs/tvr_Mesa de trabajo 1.png'
import Link from "gatsby-link";

const useStyles = createUseStyles({
    container: {
        display: 'flex',
        overflow: 'hidden',
        '@media (max-width: 620px)': {
            alignItems: 'center',
            flexDirection: 'column',
        },
    },
});

const App = () => {
    const [counter, setCounter] = useState(0);
    const classes = useStyles();
    
    useEffect(() => {
        let intervalId = setInterval(() => {
            if (counter >= 360) {
                return setCounter(0)
            }
            console.log(counter)
            
            setCounter(counter => counter + 1);
        }, 10);
        
        return () => clearInterval(intervalId);
    });
    
    return (
        <div className={classes.container}>
            <div style={{
                position: 'absolute',
                backgroundImage: `url("https://web.archive.org/web/20091021160814/http://www.geocities.com/heavensent_kennels/bkg6.gif")`,
                overflow: 'hidden',
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                paddingBottom: 50,
                width:'100vw',
                backgroundSize: 'contain',
                backgroundPositionX: -10,
                backgroundPositionY: -10,
                zIndex: -1,
            }}
            >
                <a href="https://www.cameronsworld.net">
                    <img
                        style={{width: 200}}
                        src="https://web.archive.org/web/20091027152314/http://www.geocities.com/Paris/Lights/1439/painting/dovean.gif"
                        alt=""
                    />
                </a>
                <a href="https://wwwwwwwww.jodi.org">
                    <img
                        src="https://web.archive.org/web/20091024092634/http://www.geocities.com/monstrex2000/Demon-05.gif"
                        alt=""
                    />
                </a>
                <a href="http://conspiracy.thenewinquiry.com">
                <img
                    src="https://web.archive.org/web/20090729134609/http://www.geocities.com/the_red_demon/au03.gif"
                    alt=""
                />
                </a>
                <a href="https://angeles-luz.es">
                    <img
                        src="https://web.archive.org/web/20060124023032/http://www.geocities.com/southbeach/port/1074/demon.gif"
                        alt=""
                    />
                </a>
            </div>
            <a>
                <img
                    style={{width: '100vw', position: 'fixed', bottom:0, left:0, pointerEvents: 'none'}}
                    src="https://web.archive.org/web/20090821071739/http://geocities.com/tour_of_heaven/anigates.gif"
                    alt=""
                />
            </a>
        </div>
    );
}

export default App
